import React, { useState } from "react";
import "../StylesPages/LoginForm.css";
import { Link, useNavigate } from "react-router-dom";
import Swal from "sweetalert2";
import { useUserContext } from "../UserContext";

const LoginForm = () => {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [acceptedTerms, setAcceptedTerms] = useState(false);
  const { setUser } = useUserContext(); // Accede a la funcin setUser desde el UserContext
  const navigate = useNavigate(); // Hook useNavigate para redirigir despus del inicio de sesin

  const handleLogin = async (e) => {
    e.preventDefault();

    if (!acceptedTerms) {
      Swal.fire({
        icon: "error",
        title: "Error al enviar datos",
        text: "Debes aceptar los trminos y condiciones para continuar.",
      });
      return;
    }

    try {
      // Enva las credenciales al backend para obtener el resultado del inicio de sesin
      const response = await fetch("http://localhost:4000/login", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          username,
          password,
        }),
      });

      const data = await response.json();

      if (!response.ok) {
        throw new Error(data.message);
      }

      const { success, user } = data;

      if (success) {
        // Guarda los datos del usuario en el contexto
        setUser(user);

        Swal.fire({
          icon: "success",
          title: "Datos enviados",
          text: "Has iniciado sesin correctamente.",
        }).then(() => {
          navigate("/"); // Redirige a la pgina de inicio despus del inicio de sesin exitoso
        });
      } else {
        Swal.fire({
          icon: "error",
          title: "Error al enviar datos",
          text: "Credenciales invlidas. Por favor, verifica tu nombre de usuario y contrasea.",
        });
      }
    } catch (error) {
      Swal.fire({
        icon: "error",
        title: "Error al enviar datos",
        text:
          error.message ||
          "Ocurri un error al iniciar sesin. Por favor, intenta de nuevo ms tarde.",
      });
    }
  };

  return (
    <div className="login-form">
      <form className="form" onSubmit={handleLogin}>
      <h2 className="login-form-title">Iniciar Sesin</h2>

        <input
          type="text"
          value={username}
          onChange={(e) => setUsername(e.target.value)}
          placeholder="Usuario"
          required
        />
        <input
          type="password"
          value={password}
          onChange={(e) => setPassword(e.target.value)}
          placeholder="Contrasea"
          required
        />
        <br />

        <button className="form-btn" type="submit">
          Ingresar
        </button>
        <div className="restart-password">
          <Link to="#">Olvidaste tu contrasea</Link>
          <div className="terms">
            <input
              type="checkbox"
              checked={acceptedTerms}
              onChange={() => setAcceptedTerms(!acceptedTerms)}
              required
            />
            <label>Acepto los trminos y condiciones</label>
          </div>
        </div>
      </form>
    </div>
  );
};

export default LoginForm;
